import React from 'react';
import { TypoH3 } from '../Typo';
import { Container, Figure, Grid, Video } from './styles';
import { theme } from '../../styles/colors';

import Zoom from 'react-medium-image-zoom';
import 'react-medium-image-zoom/dist/styles.css';

export function Gallery({
  title,
  gridColumnMobile,
  gridColumnTablet,
  children,
}) {
  return (
    <Container>
      <TypoH3>{title}</TypoH3>
      <Grid
        gridColumnMobile={gridColumnMobile}
        gridColumnTablet={gridColumnTablet}
      >
        {children}
      </Grid>
    </Container>
  );
}

export function GalleryItem({
  title,
  noZoom,
  imgSource,
  gridColumn,
  height,
  ...rest
}) {
  return (
    <Figure gridColumn={gridColumn} {...rest}>
      {noZoom ? (
        <img
          src={imgSource}
          alt={title}
          height={'100%'}
          width={'100%'}
          loading={'lazy'}
        />
      ) : (
        <Zoom overlayBgColorEnd={theme.t001.a}>
          <img
            src={imgSource}
            alt={title}
            height={'100%'}
            width={'100%'}
            loading={'lazy'}
          />
        </Zoom>
      )}
      <figcaption>{title}</figcaption>
    </Figure>
  );
}

export function GalleryVideo({
  title,
  vidSource,
  gridColumn,
  height,
  ...rest
}) {
  return (
    <Video>
      <video
        height={window.innerHeight * 0.8}
        width={'100%'}
        controls
        gridColumn={gridColumn}
        {...rest}
      >
        <source src={vidSource}></source>
        Your browser does not support the video tag.
      </video>
      <caption>{title}</caption>
    </Video>
  );
}
